import { useState } from 'react'
import Flatpickr from 'react-flatpickr'
import { addprize } from '../store/actions'
import { useDispatch } from 'react-redux'
import { Card, CardHeader, CardTitle, CardBody, Label, Button } from 'reactstrap'
import {
  AvForm,
  AvGroup,
  AvField,
  AvInput,
  AvFeedback

} from 'availity-reactstrap-validation-safe'

import '@styles/react/libs/flatpickr/flatpickr.scss'

const Validation = () => {
    const dispatch = useDispatch()
    const [prizeform, setPrizeform] = useState({name:"", caption:"", value:"", category:"",  expires_at: "", assets_url:""})
    const onSubmit = () => {
      dispatch(addprize(prizeform))
        // setPrizeform({ expires_at: dater})
        console.log(prizeform)
      }
      const edittime = (date) => {
    
        const local = new Date(date)
        const datestring = `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()} ${local.getHours()}:${local.getMinutes()}`
        setPrizeform({ ...prizeform, expires_at: datestring })
        console.log(prizeform)
        // console.log(prizeform.value) 
        
      }
      const convertBase64 = (file) => {
        return new Promise((resolve, reject) => {
          const fileReader = new FileReader()
          fileReader.readAsDataURL(file)
          
          fileReader.onload = () => {
            resolve(fileReader.result)
          }
          
          fileReader.onerror = (error) => {
            reject(error)
          }
        })
      }
      
      const uploadImage = async (e) => {
        const file = e.target.files[0]
        const base64 = await convertBase64(file)
        setPrizeform({ ...prizeform, assets_url: base64 })
      }
      function handleChange(evt) {
        console.log(evt)
          const value = evt.target.value ? evt.target.value : evt.value
          // setClear({
          //   ...clear,
          //   [evt.target.name]: value
          // })
          console.log(value)
          if (evt.target.type === 'text' || evt.target.type === 'number') {
            setPrizeform({
              ...prizeform,
              [evt.target.name]: value
            })
          } else if (evt.label) {
            setPrizeform({
              ...prizeform,
              [evt.target.name]: value
            }) 
          } else {
            
            uploadImage(evt)
          }    
      }
  return (
    <Card>
      <CardHeader>
        <CardTitle tag='h4'>Add Prize</CardTitle>
      </CardHeader>
      <CardBody>
        <AvForm>
          <AvGroup>
            <Label for='name'>Name</Label>
            <AvInput 
             type="text"
             name="name"
             id="name"
             onChange={handleChange}
             placeholder="Enter Product Name" 
             required 
            />
            <AvFeedback>Please enter a valid name!</AvFeedback>
          </AvGroup>
          <AvGroup>
            <Label for='caption'>Caption</Label>
            <AvInput 
             type="text"
             name="caption"
             id="caption"
             rules={{ required: true }}
             onChange={handleChange}
             placeholder="Enter Product Name"
             required 
            />
            <AvFeedback>Please enter a valid caption!</AvFeedback>
          </AvGroup>
          <AvGroup>
            <Label for='select'>Category</Label>
            <AvField 
            type='select' 
            name='category' 
            id='select' 
            //onChange={handleChange}
            onChange={(e) => setPrizeform({...prizeform, category: e.target.value})}
            required>
              
               <option value='Fashion'>Fashion</option>
              <option value='Coupon'>Coupon </option>
              <option value='Electronics'>Electronics</option>
             
            </AvField>
            <AvFeedback>Please select a country</AvFeedback>
          </AvGroup>
          <AvGroup>
            <Label for='coin'>Coin</Label>
            <AvInput 
            type='number'
            name='value'
            id='coin' 
             min = '1'
            onChange={handleChange}
            placeholder="Enter No of Coins "
            required />
            <AvFeedback>Please enter Bio!</AvFeedback>
          </AvGroup>
          <AvGroup>
            <Label for='picker'>Valid till</Label>
            <AvField
              data-enable-time
              id='picker'
              tag={Flatpickr}
              name='picker'
              value={prizeform.expires_at}
              onChange={(date) => edittime(date)}
               className='form-control'
              options={{
                enableTime: true,
                dateFormat: "Y-m-d H:i:s",
                minDate: "today"
              }}
              // onChange={(selectedDates, dateStr) => {
              //   const firstDate = selectedDates[0]
              //   setPrizeform({ ...prizeform, expires_at:dateStr})
              //   console.log({firstDate, dateStr })
              // }}
              required
            />
            <AvFeedback>Please enter your Date!</AvFeedback>
          </AvGroup>
         
          <AvGroup>
            <Label for='file'>Image</Label>
            <AvInput
             type="file"
             id="file"
             name="file"
              className='form-control'
             onChange={handleChange}
             required 
             />
            <AvFeedback>Please enter file!</AvFeedback>
          </AvGroup>
         <Button
          color='primary'
          type='submit'
          onClick={(e) => onSubmit(e)}
          >
            Submit
          </Button>
        </AvForm>
      </CardBody>
    </Card>
  )
}

export default Validation